import React, { Component } from 'react';
import InfoBoundaries from './information/infoBoundaries';
import ProBoundaries from './procesos/proBoundaries';
import ListasBoundaries from './listas/listasBoundaries';

import {Tabs, Tab} from 'react-bootstrap';


export class Boundaries extends Component {
    render() {
        return (
            <div style={{overflowY:"scroll",height:"53rem", marginRight:"2rem"}}>
                <div style={{marginRight:"5rem"}}>
                    <Tabs defaultActiveKey="Informaci√≥n" id="uncontrolled-tab-example" className="m-2">
                        <Tab eventKey="Informaci√≥n" title="Informaci√≥n">
                            <div className="container">
                                <InfoBoundaries />
                            </div>                            
                        </Tab>
                        <Tab eventKey="Proceso" title="Proceso">
                            <div className="container">
                                <ProBoundaries />
                            </div>                            
                        </Tab>
                        <Tab eventKey="Lista de Chequeo" title="Lista de Chequeo">
                            <div className="container">
                                <ListasBoundaries />
                            </div>
                        </Tab>
                    </Tabs>
                </div>
            </div>
        )
    }
}

export default Boundaries
