import React, { Component } from 'react';
import InfoEcuador from './information/infoEcuador';
import ProEcuador from './procesos/proEcuador';
import ListasEcuador from './listas/listasEcuador';
import {Tabs, Tab} from 'react-bootstrap';


export class Ecuador extends Component {

    

    render() {
        return (
            <div style={{overflowY:"scroll",height:"53rem", marginRight:"2rem"}}>
                <div style={{marginRight:"5rem"}}>
                    <Tabs defaultActiveKey="Informaci√≥n" id="uncontrolled-tab-example" className="m-2">
                        <Tab eventKey="Informaci√≥n" title="Informaci√≥n">
                            <div className="container">
                                <InfoEcuador />
                            </div>                            
                        </Tab>
                        <Tab eventKey="Proceso" title="Proceso">
                            <div className="container">
                                <ProEcuador />
                            </div>                            
                        </Tab>
                        <Tab eventKey="Lista de Chequeo" title="Lista de Chequeo">
                            <div className="container">
                                <ListasEcuador />
                            </div>
                        </Tab>
                    </Tabs>
                </div>
            </div>
        )
    }
}

export default Ecuador
